<template>
   

    <div  class="layout-px-spacing mt-3">
        <div class = "my-8">
       <ul class="navbar-nav flex-row mt-2">
      <li>
        <div class="page-header">
          <nav class="breadcrumb-one" aria-label="breadcrumb">
            <ol class="breadcrumb">
              <li class="breadcrumb-item"><a href="javascript:;">Masters</a></li>
              <li class="breadcrumb-item active" aria-current="page"><span>Fuel Statement</span></li>
            </ol>
          </nav>
        </div>
      </li>
    </ul>
    </div>

    <div class="d-flex flex-wrap gap-2 mb-2 mt-4 justify-content-center align-items-center position-relative">
                <div v-if="view_distribution && !loading_spinner" class="d-flex align-items-center">
                  <h4 class="text-dark text-uppercase mb-0">
                   {{ Object.values(groupedDistribution)[0]?.[0]?.item_name }} Distribution Statement For {{ Object.values(groupedDistribution)[0]?.[0]?.machine }} {{ Object.values(groupedDistribution)[0]?.[0]?.type }}
                  </h4>
                </div>
                
                <button class="btn btn-success position-absolute end-0" @click="printStatement()">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-printer me-1">
                            <polyline points="6 9 6 2 18 2 18 9"></polyline>
                            <path d="M6 18H4a2 2 0 0 1-2-2v-5a2 2 0 0 1 2-2h16a2 2 0 0 1 2 2v5a2 2 0 0 1-2 2h-2"></path>
                            <rect x="6" y="14" width="12" height="8"></rect>
                        </svg>
                    </button>
                </div>
    

         
               <div v-if="view_distribution" class="panel p-0" >
                    <div class="custom-table table3 ">
                           <div v-if="!loading_spinner" class=" col-12 d-flex align-items-center justify-content-between">
                        <div class=" d-flex flex-column align-items-start p-1 ">                                                                  
                        </div>  
                           </div>
                        
             
                 
   
                    

      

  
         <div class="mt-5" v-if="loading_spinner" v-html="tableShimmer()"></div>

        <div v-if="!loading_spinner" class="mt-4">
          <div v-for="(projectData, projectName) in groupedDistribution" :key="projectName" class="mb-4">
            <div class="bg-light p-3 ">
              <div class="d-flex justify-content-between align-items-center">
                <h5 class="text-dark mb-0">
                  {{ projectName === 'no_project' ? 'Out Of Project' : projectName }}
                </h5>
                <button 
                  class="btn btn-success btn-sm my-1" 
                  @click="printProjectData(projectData, projectName)"
                  title="Print statement"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-printer me-1" viewBox="0 0 16 16">
                    <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1"/>
                    <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1"/>
                  </svg>
                </button>
              </div>
          <div class="custom-table table3">
           <div class="panel p-0">
              <v-client-table 
                :data="projectData" 
                :columns="distributionColumn" 
                :options="table_option"
              >
                <template #actions="props">
                   <div class="position-relative d-inline-block">
    <svg 
      xmlns="http://www.w3.org/2000/svg" 
      width="26" 
      height="26" 
      fill="currentColor" 
      viewBox="0 0 24 24"
      style="cursor: pointer;"
      title="Add Remark"
      data-bs-toggle="modal" data-bs-target="#edit"
      @click="statement = props.row">
      
      <!-- Speech Bubble -->
      <path d="M4 4h16a2 2 0 0 1 2 2v10a2 2 0 0 1-2 2H7l-4 4v-16a2 2 0 0 1 2-2z" 
            stroke="currentColor" 
            stroke-width="2" 
            fill="none" 
            stroke-linejoin="round"/>
      
      <!-- Horizontal Remark Lines -->
      <line x1="7" y1="9" x2="17" y2="9" stroke="currentColor" stroke-width="2" stroke-linecap="round"/>
      <line x1="7" y1="13" x2="14" y2="13" stroke="currentColor" stroke-width="2" stroke-linecap="round"/>
      
      <!-- Red notification circle for remarks -->
      <circle
        v-if="props.row.remark"
        cx="19" cy="6" r="6" fill="red" stroke="white" stroke-width="1.5"/>
    </svg>

    <!-- Black notification circle for missing readings -->
    <svg 
      v-if="!props.row.initial || !props.row.final || parseFloat(props.row.initial) === 0 || parseFloat(props.row.final) === 0"
      xmlns="http://www.w3.org/2000/svg" 
      width="20" 
      height="20" 
      fill="currentColor" 
      class="bi bi-exclamation-triangle-fill text-warning position-absolute top-0 start-100 translate-middle" 
      viewBox="0 0 16 16"
      title="Missing readings"
      style="cursor: pointer; z-index: 10;">
      <path d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5m.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2"/>
    </svg>
  </div>
                </template>
              </v-client-table>
            </div>
            </div>
            </div>
          </div>
        </div>
        
                    </div>
                </div>
      
  <!-- distribution ends -->

    </div>
  
  <!-- edit modal starts -->
 <div class="modal fade mymodal" id="edit" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
  <div class="modal-dialog modal-xl">
    <div class="modal-content" >
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="staticBackdropLabel">   {{ statement.type }} - {{ statement.machine }} CONSUMPTION  SUMMARY</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" id="close-modal" ></button>
      </div>
<div class="modal-body" >
<div class="card mb-3">
  <div class="card-body">
    <div class="row mb-2 text-start">
      <div class="col-md-4"><strong>Issued Date:</strong> {{statement.issued_date}}</div>
      <div class="col-md-4"><strong>Issuer:</strong> {{ statement.issuer }}</div>
      <div class="col-md-4"><strong>Driver:</strong> {{ statement.driver }}</div>
    </div>
    <div class="row mb-2 text-start">
      <div class="col-md-4"><strong>Machine:</strong> {{ statement.machine }}</div>
      <div class="col-md-4"><strong>Type:</strong> {{ statement.type }}</div>
      <div class="col-md-4"><strong>Issued:</strong> {{ statement.fuel }} Ltrs</div>
    </div>
    <div class="row mb-2 text-start">
      <div class="col-md-4"><strong>Initial:</strong> {{statement.initial}}</div>
      <div class="col-md-4"><strong>Final:</strong> {{ statement.final }}</div>
      <div class="col-md-4"><strong>Ratio:</strong> {{ statement.ratio }}</div>
    </div>
     <div class="row mb-2 text-start">
      <div class="col-md-4 my-2"><strong>consumption type:</strong> {{ statement?.consumption_type }}</div>
      <div class="col-md-4 my-2">
        <strong>Work:</strong> 
        {{ statement?.work }} 
        {{ statement?.consumption_type === 'Km/Litre' ? 'Km' : statement?.consumption_type === 'Litre/hr' ? 'hr' : '' }}
      </div>
      <div class="col-md-4 my-2"><strong>Used:</strong> {{ statement?.used }} Ltrs</div>
      <div class="col-md-12"><strong>Task:</strong> {{ statement?.task }}</div>
    </div>

     <div v-if="statement.remark" class="row text-start">
      <div class="col-md-12"><strong>Remark :</strong> {{ statement.remark }}</div>
    </div>
  </div>
</div>


<form
v-if="user_permissions.includes('can_issue_fuel') || user_permissions.includes('is_md')"
@submit.prevent="updateReadings()">
   <div class="row layout-top-spacing" >
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">
                 <div class="row g-3">
  <div class="col-md-4" v-if="user_permissions.includes('can_issue_fuel')">
    <label class="form-label">Initial Reading</label>
    <input class="form-control" type="number" step="any" placeholder="Enter Initial Reading" v-model="statement.initial">
  </div>

  <div class="col-md-4" v-if="user_permissions.includes('can_issue_fuel')">
    <label class="form-label">Final Reading</label>
    <input class="form-control" type="number" step="any" placeholder="Enter Final Reading" v-model="statement.final">
  </div>

   <div class="col-md-11" v-if="user_permissions.includes('is_md')">
    <label class="form-label">Enter Remark</label>
    <input class="form-control" type="text" step="any" placeholder="Enter Remark" v-model="statement.remark">
  </div>


  <div class="col-md-1 d-flex justify-content-between align-items-center">
    <div class="pt-4">
      <button class="btn btn-success" :disabled="isSending">
        <span v-if="isSending" v-html="loadingSpinner()"></span>
        <span v-else> Save</span>
       </button>
    </div>
  </div>
</div>

                    </div>
                </div>
            </div>
        </div>
</form>
   
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-danger btn-sm py-2 px-2" data-bs-dismiss="modal" id="close-modal">Close</button>
      </div>

    </div>
  </div>

</div>

   <!-- edit modal ends -->
</template>

<style>
    .table3 .actions svg {
        padding: 2px;
    }
</style>

<script setup>
    import axios from 'axios';
    import { onMounted, ref } from 'vue';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css'
    import '@/assets/sass/elements/tooltip.scss';
    import { useMeta } from '@/composables/use-meta';
    import { useRoute } from 'vue-router';
    import { inject } from 'vue';
    import Multiselect from '@suadelabs/vue3-multiselect';
    
    const showMessage = inject('showMessage');
    const showError = inject('showError');
    const loadingSpinner = inject('loadingSpinner');
    const formatDate = inject('formatDate');
    const formatNumber = inject('formatNumber');
    const tableShimmer = inject('tableShimmer')
    const statement = ref('')
    const isClosed = ref(true)
    const isDiesel = ref(true);
    const route = useRoute();
    useMeta({ title: 'Purchase Order' });
    const activeTab = ref("dieseldistribution");
    const machine_field = ref(false);
    const driver_name = ref('');
    const initial_reading= ref(' ');
    const final_reading = ref('');
    const task = ref('');
    const fuel_id = ref(364);
    const machine = ref('');
    const fuel_quantity = ref('');
    const issued_date = ref(new Date().toISOString().substr(0, 10));
    const details = ref('');
    const ratio = ref('');   
    const Machines = ref([]);
    const user_permissions = JSON.parse(localStorage.getItem("user")).permissions;
    const bulkDistribution = ref([]);
    const distributionColumn = ref(['SNo','issuer', 'driver', 'task','issued','used','balance','ratio','rate','cost','used_cost','issued_date','actions']);
    const table_option = ref({
        perPage: 50,
        perPageValues: [  50 ,100,200,500],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['SNo','date_issued','ratio',],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });
  
    const view_machines = ref(false);
    const view_distribution = ref(true);
    const loading_spinner = ref(false);   
    const selectedMachine = ref('');
    const total_fuel = ref(0);
      
    const isfilling  = ref(false);
    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });

    const viewDistribution = ()=>{
    
      getBulkDistribution();     
      view_machines.value=false;
      view_distribution.value=true;
    }


    const groupedDistribution = ref({});

    const getBulkDistribution = async () => {
        loading_spinner.value = true; 
        const asset_id = route.params.asset_id; 
        let url = "machine-statement";
        if (asset_id === "all") {
            url += "/all";
        } else if (asset_id) {
            url += `/${asset_id}`;
        }  
    
        try {
            const response = await axiosInstance.get(url);
            const groupedData = response.data;
            
            // Process each project group
            const processedGroups = {};
            Object.keys(groupedData).forEach(projectKey => {
                const projectStatements = groupedData[projectKey]
                    .map((statement, index) => {
             
    const rateNumber = Number(statement.rate); 
    const costNumber = rateNumber * statement.quantity;

              const workDone = statement.final_reading - statement.initial_reading;
              const averageRatio = statement.machine_average_ratio || 0;
              const consumptionType = statement.machine_consumption_type;

    let usedFuel = 0;
    if (averageRatio > 0 && workDone > 0) {
        if (consumptionType === "Litre/hr") {
            usedFuel = (averageRatio * workDone).toFixed(2);
        } else {
            usedFuel = (workDone / averageRatio).toFixed(2);
        }
    }
                 
   return {
      ...statement,    
        SNo: index + 1,
        work: (statement.final_reading - statement.initial_reading).toFixed(2), 
        fuel: statement.quantity,
        issued: statement.quantity,
        used: (!statement.initial_reading || !statement.final_reading || parseFloat(statement.initial_reading) === 0 || parseFloat(statement.final_reading) === 0) 
          ? "---" 
          : usedFuel,
        balance: (!statement.initial_reading || !statement.final_reading || parseFloat(statement.initial_reading) === 0 || parseFloat(statement.final_reading) === 0) 
          ? "---" 
          : (statement.quantity - usedFuel).toFixed(2),
                            item_name: statement.item_name,
                            consuption: statement.consumption_type,
                            issued_date: new Date(statement.created_at).toLocaleDateString('en-GB', {
                                day: '2-digit',
                                month: '2-digit', 
                                year: 'numeric'
                            }),
                            task: statement.task || '--',
                            remark: statement.remark,
                            driver: statement.driver_name || '--',
                            issuer: statement.issuer_name,
                            initial: statement.initial_reading,
                            final: statement.final_reading,
                            machine: statement.machine_name,
                            used_cost_raw: usedFuel * rateNumber, 
                            type: statement.machine_type,
                            consumption_type: statement.machine_consumption_type,
                            ratio: statement.machine_average_ratio,
                            id: statement.id,
                            used_cost: formatNumber(usedFuel * rateNumber), 
                            rate: formatNumber(rateNumber), 
                            cost: formatNumber(costNumber), 
                            cost_original :costNumber,
                        };
                    });
            
                if (projectStatements.length > 0) {
                    processedGroups[projectKey] = projectStatements;
                }
            });

            groupedDistribution.value = processedGroups;
            
            // Calculate total fuel across all projects
            const allStatements = Object.values(processedGroups).flat();
            total_fuel.value = allStatements.reduce((sum, statement) => {
                return sum + Number(statement.fuel);
            }, 0);
           
            loading_spinner.value = false;
        } catch (error) {
            showError(error.message);
            loading_spinner.value = false;
            console.error("Error fetching Bulk Distribution: ", error);
        }
    };

    const registerMachineDistribution = () => {
            isfilling.value=false;
         const itemData = {
            
            asset_id:selectedMachine.value.id,
            consumption_type: ratio.value,
            issuer_id: JSON.parse(localStorage.getItem("user")).id,
            quantity:fuel_quantity.value,
            usage_date: issued_date.value,
            item_id:isDiesel.value ? 364 : 365,
            initial_reading: initial_reading.value,
            final_reading: final_reading.value,
            task: task.value,
            driver_name: driver_name.value
 
        };
   
    loading_spinner.value = true;
        axiosInstance
            .post('asset-consumptions', itemData)
            .then((response) => {
                getBulkDistribution();
                selectedMachine.value = '';
                fuel_quantity.value = '';
                details.value = '';
                ratio.value = '';
                driver_name.value = '';
                initial_reading.value = '';
                final_reading.value = '';
                task.value = '';
                isClosed.value = true;
                loading_spinner.value = false;
                // Display success or error messages based on response status
               
            if (response.status === 201) {
                showMessage("Fuel distributed successfully");
            } else {
                console.error("Error registering Distribution:", response.data);
                showError(response.data.message || "An error occurred while registering the distribution.");
            }
            })
            .catch((error) => {
                console.error("Error registering Distribution:", error);
                showError("Error code :"+ error.message);
            });
    };


//method for fetching remarks
const name =  ref();
const user_role = JSON.parse(localStorage.getItem("user")).role;
const isfetching = ref(false);   
const status = ref("unchecked"); // Default status
const usage = ref(); // Default usage ID
const newremark = ref();
const remarks = ref([]);
const showRemarkModal = ref(false);
const useId =  JSON.parse(localStorage.getItem("user")).id;
const isSending = ref(false);

   const openRemarkModal= (row) => {
    fetchRemarks(row.id);
    name.value= row.machine
    usage.value = row.id;     
        showRemarkModal.value = true;
    }
  const   closeRemarkModal = () => {
    showRemarkModal.value = false;
    newremark.value = ''; // Clear the remark input
    remarks.value = []; // Reset the remarks list
    name.value = ''; // Clear the machine name
    
    }


   const fetchRemarks = async (id) => {
    isfetching.value = true;

    try {
        const response = await axiosInstance.get(`/usage-remark/${id}`);
        remarks.value = response.data.filter(item => item.remark_type === 'distribution');
    } catch (error) {
        console.error("Error fetching remarks:", error);
    }

    isfetching.value = false;
}

    const saveRemark = async () => {
        if (!newremark.value.trim()) {
            showError("Remark cannot be empty!");
            return;
        }
   isSending.value= true;
        try {
            const response = await  axiosInstance.post("/remark", {   
                created_at: new Date().toISOString(), 
                remarker_id: JSON.parse(localStorage.getItem("user")).id,
                description: newremark.value,
                status: status.value, // Default to unchecked
                type_id:usage.value,
                remark_type:"distribution"  
            });

            remarks.value.push(response.data); // Add new remark to the list
            newremark.value = ""; // Clear input field
            showMessage("Remark added:", "success");
        } catch (error) {
            showError("Error adding remark:", error);
        }
        isSending.value= false;
    };


    const updateReadings = async()=>{
      isSending.value = true
      const payload = {
        initial_reading : statement.value.initial,
        final_reading :statement.value.final,
        remark : statement.value.remark,
        task: statement.value.task
      }

      try{
        const response = await axiosInstance.put(`asset-consumptions/${statement.value.id}`,payload)
        showMessage("Readings Updated")
        getBulkDistribution()
        document.getElementById('close-modal').click()
      }catch(error){
        showError(error.message)
        console.log(error)
      }finally{
        isSending.value = false
      }
    }

const printStatement = () => {
  if (!groupedDistribution.value || Object.keys(groupedDistribution.value).length === 0) {
    showError("No distribution data available to print");
    return;
  }

  const iframe = document.createElement("iframe");
  iframe.style.display = "none";
  document.body.appendChild(iframe);

  const allData = Object.values(groupedDistribution.value).flat();

  const printContent = `
<!DOCTYPE html>
<html>
<head>
<title>Fuel Distribution Report</title>
<style>
  body { font-family: 'Courier New', monospace; margin: 40px; font-size: 13px; }
  .text-style { font-family: 'Courier New', monospace; }
  .company-title { text-align: center; margin-bottom: 10px; }
  .title { text-align: center; font-size: 16px; margin: 10px 0; text-transform: uppercase; }
  table { width: 100%; border-collapse: collapse; margin-top: 10px; }
  th, td { border: 0.5px solid #000; padding: 6px; font-size: 11px; }
  th { background-color: #f8f8f8; font-weight: bold; text-align: center; }
  .text-right { text-align: right; }
  .text-center { text-align: center; }
  hr { border: 0; border-top: 0.5px solid #000; margin: 10px 0; }
  @media print { body { margin: 10mm; } }
</style>
</head>
<body>

<div class="company-title text-style">
  <i>
    <h1>COPEX CONTRACTORS CO.LTD</h1>
    <div><b>Address:</b> Msamvu, P.O.BOX 678, Morogoro</div>
    <div><b>Tel:</b> 0713 000 777, 0657 111 555</div>
    <div><b>Email:</b> info@copexcontractors.co.tz</div>
    <div><b>TIN:</b> 135-489-247 &nbsp;&nbsp; <b>VRN:</b> 40-032826C</div>
  </i>
</div>

<hr>

<div class="title text-style">
   Distribution Statement For ${allData[0]?.machine || 'N/A'} ${allData[0]?.type || 'N/A'}
</div>

<hr>

${Object.entries(groupedDistribution.value).map(([projectName, projectData]) => `
<h3 class="text-style">${projectName === 'no_project' ? 'Out Of Project' : projectName}</h3>

<table>
  <thead>
    <tr>
      <th>S/No</th>
      <th>Issuer</th>
      <th>Driver</th>
      <th>Task</th>
      <th>Issued</th>
      <th>Used</th>
      <th>Balance</th>
      <th>Ratio</th>
      <th>Rate</th>
      <th>Cost</th>
      <th>Used Cost</th>
      <th>Date</th>
    </tr>
  </thead>
  <tbody>
    ${projectData.map((item, index) => `
      <tr>
        <td class="text-center">${index + 1}</td>
        <td>${item.issuer || 'N/A'}</td>
        <td>${item.driver || 'N/A'}</td>
        <td>${item.task || 'N/A'}</td>
        <td class="text-right">${item.issued || '0'}</td>
        <td class="text-right">${item.used || '---'}</td>
        <td class="text-right">${item.balance || '---'}</td>
        <td class="text-right">${item.ratio || 'N/A'}</td>
        <td class="text-right">${item.rate || '0'}</td>
        <td class="text-right">${item.cost || '0'}</td>
        <td class="text-right">${item.used_cost || '0'}</td>
        <td>${item.issued_date || 'N/A'}</td>
      </tr>
    `).join('')}
    <tr>
      <td colspan="4" class="text-right"><b>Project Total:</b></td>
      <td class="text-right"><b>${projectData.reduce((sum, item) => sum + Number(item.issued), 0).toFixed(2)}</b></td>
      <td class="text-right"><b>${projectData.reduce((sum, item) => {
        const used = item.used === '---' ? 0 : Number(item.used);
        return sum + used;
      }, 0).toFixed(2)}</b></td>
      <td class="text-right"><b>${projectData.reduce((sum, item) => {
        const balance = item.balance === '---' ? 0 : Number(item.balance);
        return sum + balance;
      }, 0).toFixed(2)}</b></td>
      <td colspan="2"></td>
      <td class="text-right"><b>${projectData.reduce((sum, item) => sum + Number(item.cost_original || 0), 0).toFixed(2)}</b></td>
      <td class="text-right"><b>${projectData.reduce((sum, item) => sum + Number(item.used_cost_raw || 0), 0).toFixed(2)}</b></td>
      <td></td>
    </tr>
  </tbody>
</table>
`).join('')}

<hr>

</body>
</html>
`;

  const iframeDoc = iframe.contentWindow.document;
  iframeDoc.open();
  iframeDoc.write(printContent);
  iframeDoc.close();

  iframe.onload = () => {
    iframe.contentWindow.focus();
    iframe.contentWindow.print();
    setTimeout(() => document.body.removeChild(iframe), 1000);
  };
};

const printProjectData = (projectData, projectName) => {
