  document.body.appendChild(iframe);

  const totalIssued = projectData.reduce((sum, item) => sum + Number(item.issued), 0);
  const totalUsed = projectData.reduce((sum, item) => {
    const used = item.used === '---' ? 0 : Number(item.used);
    return sum + used;
  }, 0);
  const totalBalance = projectData.reduce((sum, item) => {
    const balance = item.balance === '---' ? 0 : Number(item.balance);
    return sum + balance;
  }, 0);
  const totalCost = projectData.reduce((sum, item) => sum + Number(item.cost_original || 0), 0);
